            this.CheckDirty = function(Obj1, Obj2, isStrict=true){
                if (!Obj1 || !Obj2)
                    return false;
                let isEqual = true;
                if (Obj1 instanceof Array){
                    if (isStrict && Obj1.length != Obj2.length){
                        return false;
                    }
                    for (let i = 0; i < Obj1.length; i++){
                        if (Obj1[i] instanceof Object){
                            isEqual = Lure.CheckDirty(Obj1[i], Obj2[i], isStrict)
                            if (!isEqual){
                                return false;
                            }
                            continue;
                        }
                        if (Obj1[i] !== Obj2[i]){
                            return false;
                        }
                    }
                }
                else if (Obj1 instanceof Object){
                    if (isStrict && Object.keys(Obj1).length !== Object.keys(Obj2).length)
                        return false;
                    for (let i in Obj1){
                        if (Obj1[i] instanceof Object){
                            isEqual = Lure.CheckDirty(Obj1[i], Obj2[i], isStrict)
                            if (!isEqual){
                                return false;
                            }
                            continue;
                        }
                        if (Obj1[i] !== Obj2[i]){
                            return false;
                        }
                    }
                }
                else {
                    return Obj1 === Obj2
                }
                return isEqual;
            };
